.contact-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact-left-container,
.contact-right-container {
  width: 40%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem;
}

.contact-image {
  width: 80%;
  border-radius: 1rem;
}

.contact-main-title {
  position: relative;
  display: block;
  width: 35%;
  text-align: left;
}

.contact-list-item {
  margin-top: 1rem;
  display: flex;
  align-items: center;
}

.contact-list-item svg {
  margin-right: 1rem;
}

.item-account-link {
  position: relative;
  z-index: 50;
}

@media (max-width: 576px) {
  .contact-container {
    flex-direction: column;
  }

  .contact-left-container,
  .contact-right-container {
    width: 90%;
  }

  .contact-main-title {
    width: 60%;
  }
}
