.modal-backdrop {
  position: fixed;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.6);
}

.modal-content-wrapper {
  position: fixed;
  width: 80%;
  height: 98vh;
  margin: auto;
  left: 10%;
  top: 1%;
  padding: 3rem;
  z-index: 51;
  overflow-y: scroll;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

@media (max-width: 576px) {
    .modal-content-wrapper {
        width: 90%;
        left: 5%;
    }
}
