.hamburger-menu-container {
  display: flex;
  align-items: center;
}

.hamburger-icon {
  cursor: pointer;
  z-index: 100;
  color: var(--title-color);
  transition: all 250ms ease-in-out;
}

.hamburger-icon.close {
  color: var(--title-color);
}

.menu-container {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 90;
  min-width: 300px;
  width: 100%;
  max-width: 30%;
  height: 100%;
  background-color: var(--body-color);
  box-shadow: var(--shadow);
  padding: 1rem 2.5rem;
  transform: translateX(4rem);
}
