.projects-container {
  padding: 2rem;
}

.projects-main-title {
  width: 70%;
  margin: auto;
  position: relative;
  text-align: center;
}

.projects-main-title::after {
  content: '';
  position: absolute;
  left: 50%;
  top: 2.5rem;
  transform: translateX(-50%);
  background-color: var(--title-color);
  width: 60px;
  height: 3px;
  border-radius: 1rem;
}

.project-list {
  display: flex;
  align-items: center;
  justify-content: center;
}

.project-list-item {
  width: 30%;
  height: 100vh;
  border: 3px solid var(--title-color);
  border-radius: 0.75rem;
  overflow: hidden;
  margin: 0.5rem;
  background-color: #000;
}

.project-image {
  width: 100%;
  height: 100%;
}

@media (max-width: 576px) {
  .project-list {
    flex-direction: column;
  }

  .project-list-item {
    width: 100%;
}
}
