.switch-container {
  position: fixed;
  top: 20px;
  z-index: 55;
  display: flex;
  align-items: center;
  width: 100px;
  justify-content: center;
}

.scroll-to-top-button {
  position: fixed;
  right: 1.25rem;
  bottom: 1.25rem;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #009edb;
  color: var(--body-color);
  z-index: 50;
}

#light {
  background-color: var(--body-color);
  color: var(--title-color);
}

#light .resume-container a {
  color: var(--body-color);
}

#light a {
  color: var(--title-color);
}

#light .modal-content-wrapper {
  background-color: var(--body-color);
}

#dark {
  background-color: #212121;
  color: var(--body-color);
}

#dark .modal-content-wrapper {
  background-color: #212121;
  color: var(--body-color);
}

#dark a {
  color: var(--body-color);
}

#dark *::after {
  background-color: var(--body-color);
}

#dark .menu-container {
  background-color: #333;
}

#dark .menu-container .copyright {
  color: var(--body-color);
}
