.about-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
}

.left-section,
.right-section {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.about-me-title,
.about-description,
.about-image {
  width: 70%;
  margin: auto;
  position: relative;
}

.about-me-title::after {
  content: '';
  position: absolute;
  left: 0;
  top: 2.5rem;
  background-color: var(--title-color);
  width: 60px;
  height: 3px;
  border-radius: 1rem;
}

.about-description {
  margin-top: 1rem;
  font-size: 0.8rem;
  line-height: 2;
}

.about-image {
  width: 80%;
  border-radius: 1rem;
}

@media (max-width: 576px) {
  .about-container {
    flex-direction: column-reverse;
  }

  .left-section,
  .right-section {
    width: 90%;
    align-items: center;
  }

  .about-me-title,
  .about-description,
  .about-image {
    width: 90%;
    margin: 0.5rem 0
  }

  .about-me-title {
    text-align: center;
  }

  .about-me-title::after {
    left: 50%;
    transform: translateX(-50%);
  }

  .about-image {
    margin-top: 1rem;
  }
}
