.status-container {
  padding: 1rem;
}

.status-list {
  display: flex;
  align-items: center;
  justify-content: center;
}

.status-card {
  margin: 1rem;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.card-icon {
    margin-bottom: 1rem;
}

.card-title {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: var(--h2-font-size);
}

.card-title strong {
    opacity: 0.5;
    font-size: var(--small-font-size);
    margin-top: 1rem;
}

@media(max-width: 576px) {
    .status-list {
        flex-direction: column;
    }
}