.navigation-menu-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.hello-text {
  text-align: center;
  margin-bottom: 1rem;
}

.hero {
  border-radius: 1rem !important;
  height: 30% !important;
  width: 100% !important;
}

.navigation-list {
  padding: 0 0.5rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-top: 3rem;
}

.navigation-link {
  font-weight: 600;
  font-size: 1.2rem;
  height: 35px;
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 0.5rem;
  display: block;
  text-align: center;
}

.navigation-link a {
  letter-spacing: 0.25rem;
  text-transform: uppercase;
  transition: all 200ms ease-in-out;
  color: var(--text-color);
}

.navigation-link a.passive {
  opacity: 0.3;
}

.copyright {
  position: absolute;
  bottom: 50px;
  font-size: 1rem;
  color: var(--title-color);
}
