.blog-container {
  margin: 1rem 0;
}

.blog-main-title {
  width: 70%;
  margin: auto;
  position: relative;
  text-align: center;
}

.blog-main-title::after {
  content: '';
  position: absolute;
  left: 50%;
  top: 2.5rem;
  transform: translateX(-50%);
  background-color: var(--title-color);
  width: 40px;
  height: 3px;
  border-radius: 1rem;
}

.blog-list {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1rem;
}

.blog-item-1 {
  width: 30%;
  height: 80vh;
  border: 3px solid var(--title-color);
  margin: 1rem;
  border-radius: 0.75rem;
  overflow: hidden;
  cursor: pointer;
  position: relative;
  z-index: 50;
  background-image: url('../../assets/images/blog-1.avif');
  background-size: 100% 90vh;
  background-repeat: no-repeat;
}

.overlay {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #009edb;
  opacity: 0;
  transition: 1s;
  border-radius: 0.75rem;
  text-align: center;
  color: var(--title-color);
}

.blog-item-1:hover .overlay {
  opacity: 1;
}

.intro-title {
  font-weight: bolder;
}

.intro-description {
  margin-top: 1rem;
  color: var(--body-color);
}

.for-more-container {
  position: absolute;
  bottom: 1rem;
  font-weight: bolder;
}

.for-more-container svg {
  width: 30px;
  height: 30px;
  margin-top: 1rem;
}

.blog-title {
  line-height: 2;
  margin: 1rem 0;
  text-align: center;
}

.blog-intro {
  line-height: 2;
  margin: 1rem 0;
  font-size: 0.8rem;
}

.blog-image {
  width: 100%;
  object-fit: contain;
  margin: 1rem;
  margin: auto;
}

.authors-title {
  text-align: right;
}

.author-name {
  text-align: right;
  margin-top: 0.5rem;
}

.copy-right {
  text-align: center;
  margin-top: 1rem;
}

@media (max-width: 576px) {
  .blog-item-1 {
    width: 90%;
    background-position: right;
  }

  .blog-title {
    text-align: left;
  }
}
