.resume-main-title {
  width: 70%;
  margin: auto;
  position: relative;
  text-align: center;
}

.resume-main-title::after {
  content: '';
  position: absolute;
  left: 50%;
  top: 2.5rem;
  transform: translateX(-50%);
  background-color: var(--title-color);
  width: 60px;
  height: 3px;
  border-radius: 1rem;
}

.resume-container {
  display: flex;
  align-items: stretch;
  margin-top: 1rem;
}

.left-container {
  width: 30%;
  padding: 1rem;
  background-color: var(--title-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.right-container {
  width: 70%;
  padding: 5rem;
  background-color: var(--container-color);
}

.resume-hero {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  box-shadow: var(--shadow);
}

.resume-name {
  font-size: var(--h2-font-size);
  color: var(--body-color);
  margin-top: 1rem;
}

.resume-title {
  color: var(--body-color);
  opacity: 0.7;
}

.divider {
  width: 90%;
  height: 2px;
  background-color: var(--body-color);
  opacity: 0.2;
  margin-top: 1rem;
}

.contact-title,
.education-title {
  font-size: 1rem;
  color: var(--body-color);
  margin: 1rem 0;
  opacity: 0.7;
  text-transform: uppercase;
}

.contact-items {
  display: flex;
  flex-direction: column;
  align-items: start;
}

.contact-item {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem;
}

.contact-item svg {
  color: var(--body-color);
  margin-right: 1rem;
}

.contact-item-description {
  color: var(--body-color);
}

.account-link {
  color: var(--body-color);
  position: relative;
  z-index: 50;
}

.education-items {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.education-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 0.5rem;
}

.education-time {
  color: var(--container-color);
  font-size: var(--small-font-size);
  text-align: center;
}

.education-name {
  color: var(--body-color);
  text-align: center;
  margin: 0.5rem 0 0.2rem;
}

.school-name {
  color: var(--container-color);
  opacity: 0.75;
  text-align: center;
}

.summary-title {
  font-size: 2rem;
  color: var(--title-color);
  margin: 1rem 0;
}

.summary-items {
  list-style-type: square;
}

.summary-item {
  margin: 0.5rem 0;
  font-size: 0.8rem;
  color: var(--title-color);
}

.job-descriptiion {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.job-title {
  color: var(--title-color);
  font-size: 1.2rem;
  font-weight: bolder;
}

.job-time {
  color: var(--text-color);
  opacity: 0.75;
  font-size: 0.75rem;
}

.company-name {
  font-weight: bolder;
  color: var(--text-color);
}

.technologie-description {
  font-size: 0.8rem;
}

.skills-container {
  margin-top: 1rem;
}

.skills-subtitle {
  color: var(--text-color);
  font-weight: bolder;
}

.skills-main-title {
  margin: auto;
  position: relative;
  color: var(--title-color);
}

.skills-main-title::after {
  content: '';
  position: absolute;
  left: 0;
  top: 2rem;
  background-color: var(--title-color);
  width: 60px;
  height: 3px;
  border-radius: 1rem;
}

.skills-box {
  padding: 1rem 0;
}

.graduation-project-container {
  margin-top: 1rem;
}

.graduation-project-main-title,
.graduation-project-subtitle,
.graduation-project-description {
  margin-bottom: 1rem;
  line-height: 2;
  color: var(--title-color);
}

.more-link {
  color: var(--title-color) !important;
  text-decoration: underline;
  text-underline-offset: 0.25rem;
  margin-left: 0.5rem;
  position: relative;
  z-index: 50;
}

@media (max-width: 576px) {
  .resume-container {
    flex-direction: column;
  }

  .left-container,
  .right-container {
    width: 100%;
  }

  .right-container {
    padding: 2rem;
  }

  .summary-title {
    font-size: 1.5rem;
  }

  .job-description {
    flex-direction: column;
    align-items: flex-start;
  }

  .skills-main-title::after {
    top: 4rem;
  }

  .skills-box {
    padding: 1.5rem 0;
  }

  .skils-items {
    flex-direction: column;
    align-items: stretch;
    justify-content: center;
  }

  .skill-card {
    margin: 1rem;
  }
}
