.skills-main-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 1rem 0;
}

.skills-front-end-container,
.skills-back-end-container {
  background-color: var(--body-color);
  width: 45%;
  padding: 1rem;
  border-radius: 1rem;
  border: 1px solid var(--title-color);
}

.skills-items {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.skills-item {
  width: 50%;
}

.item-container {
  display: flex;
  align-items: center;
}

.item-container svg {
  color: var(--text-color);
  margin-right: 0.5rem;
}

.skills-item-description {
  font-weight: bolder;
}

@media (max-width: 576px) {
  .skills-main-container {
    flex-direction: column;
  }

  .skills-front-end-container,
  .skills-back-end-container {
    width: 90%;
    margin: 1rem 0;
    font-size: 0.8rem;
  }
}
