.home-container {
  width: 100%;
  height: auto;
}

.section {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.hello-text {
  font-size: 2rem;
}

.sub-description {
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 0.25rem;
  opacity: 0.5;
  margin-top: 1rem;
}

.description {
  margin-top: 1rem;
  width: 50%;
  font-size: 0.8rem;
  line-height: 2;
}

.title-description {
  font-size: 2rem;
  margin-top: 1rem;
}

.resume-container-home {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  z-index: 50;
}

.play-button {
  width: 75px;
  margin-top: 1rem;
}

.resume-description-text {
  margin-top: 0.75rem;
  font-size: 0.8rem;
  font-weight: bold;
  text-transform: uppercase;
}

.computer-image {
  border-radius: 1rem;
  width: 50%;
  height: 300px;
  margin: 1rem 0;
  object-fit: cover;
}

.contact-detail-title {
  text-transform: uppercase;
  font-size: 10px;
  font-weight: 800;
  opacity: 0.5;
  margin: 1rem 0;
  text-align: center;
}

.email-detail,
.address-detail {
  margin-top: 0.5rem;
  font-size: 0.75rem;
  font-weight: bold;
  text-align: center;
  font-weight: bold;
}

.email,
.address {
  letter-spacing: 0;
  font-weight: var(--medium);
}

@media (max-width: 576px) {
  .home-container {
    width: 100%;
    height: auto;
  }

  .hello-text {
    font-size: 1rem;
  }

  .sub-description {
    font-size: 0.6rem;
    text-align: center;
  }

  .title-description {
    font-size: 1.5rem;
  }

  .play-button {
    width: 50px;
  }

  .computer-image {
    width: 90%;
    margin: 0.5rem 0;
  }

  .description {
    width: 90%;
  }
}
